body {
	padding-top: 60px;
	&.home {
		padding-top: 100px;
		@media (max-width: 900px) {
			padding-top: 60px;
		}
	}
}

#stek-kop {
	background-color: white;
	position: fixed;
	top: 0;
	left: 0;
	box-shadow: 0 2px rgba(255, 0, 0, 0.04);
	z-index: 20;
	width: 100%;
	.verpakking {
		display: flex;
		justify-content: space-between;
	}
	#stek-nav, #toegankelijkheid {
		box-shadow: 0 2px rgba(255, 0, 0, 0.01);
	}
}
	[data-contrast='1'] #stek-kop {
		border-bottom: 1px solid grey;
	}
	.stek-kop-links {
		a, img {
			margin: 0;
		}
		img {
			width: 279px;
			display: block;
/* 			 + img {
			 	display: none;
			 } */
		}
		@media (max-width: 600px) {
			width: 200px;
			img {
				margin-top: 8px;
			}
		}
		@media (max-width: 400px) {
			width: 160px;
			img {
				margin-top: 13px;
			}
		}
		@media (max-width: 360px) {
			width: 140px;
			img {
				margin-top: 16px;
			}
		}
		h1 {
			margin: 0;
		}
	}

	.stek-kop-rechts {
		position: relative;
		justify-content: flex-end;
	}

		.stek-kop-knoppen {

			a {
				//color: $donkergrijs;
				font-size: 2.5em;
				display: inline-block;
				line-height: 58.5px;
				margin-right: 0.4em;
				&:last-child {
					margin-right: 0;
				}
			}
			[data-toon='#stek-nav'] i {
				left: -4px;
				position: relative;
			}
			i + i {
				display: none;
			}
			.open i {
				display: none;
				& + i {
					display: inline;
				}
			}
		}

			#stek-nav, #toegankelijkheid {
				width: 100%;
				display: none;
				position: absolute;
				z-index: 3;
				.verpakking {
					justify-content: flex-end;
				}
				ul {
					padding: 20px 0;
					text-align: right;
				}
				a {
					font-size: 1.1em;
				}
				strong {
					color: $rustig_rood;
				}

			}
			#stek-nav ul {
				padding: 20px 0 0;
			}

			#zoekveld {
				display: none;
				margin-right: 20px;
				form {
					height: 60px;
				}
			}

			.zoekveldschakel-open {
				@media (max-width: 460px) {
					#toegankelijkheidsschakel, #menuschakel {
						display: none;
					}
					#zoekveldschakel {
						margin-right: 0;
					}
					#zoekveld {
						margin-right: 10px;
					}
				}
				@media (max-width: 360px) {
					/*zoek tekst veld*/
					#zoekveld form > input:first-child {
						max-width: calc(100% - 50px);
					}
				}
			}

				@media (max-width: 900px) {
					.zoekveldschakel-open {
						.stek-kop-links{
							display: none;
						}
						.stek-kop-rechts {
							width: 100%;
						}
					}
				}
				.stek-kop-knoppen {
					a {
						color: $donkergrijs;
					}
					a:hover {
						color: $rustig_rood;
					}
				}
					[data-contrast='1'] {
						.stek-kop-knoppen a {
							color: $zwart;
						}
					}


				#zoekveld {
/* 					left: 115px;
					top: 0; */
					//width: 260px;
				}
					#zoekveld input,
					#zoekveld label {
						vertical-align: middle;
						display: inline-block;
					}
						@media (max-width: 570px) {
							.screen-reader-text {
								display: none;
							}
						}
						@media (max-width: 360px) {
							#zoekveld input {
								width: 120px;
							}
						}
					[for='kop-zoekveld']:hover,
					#zoekveld input:focus + label .mdi {
						cursor: pointer;
						color: $rustig_rood;
						transition: 0.2s $ease-out-snel;
					}
						#zoekveld #kop-zoekveld {
							display: none;
						}
						#zoekveld .mdi {
							font-size: 2.5em;
						}

	#toegankelijkheid {
		a {
			transition: 0.3s opacity 0.5s;
		}
	}


	.grootte.minder,
	.grootte.herstel,
	.contrast.herstel,
	.dyslexie.herstel {
		opacity: 0.5;
		&:hover {
			cursor: normal;
		}
	}

	[data-grootte='1'],[data-grootte='2'] {
		.grootte.minder,
		.grootte.herstel{
			opacity: 1;
			&:hover {
				cursor: pointer;
			}
		}
	}

	[data-grootte='2'] {
		.grootte.meer{
			opacity: 0.5;
			&:hover {
				cursor: normal;
			}
		}
	}

	[data-contrast="1"] {
		.contrast.herstel {
			opacity: 1;
			&:hover {
				cursor: pointer;
			}
		}
		.contrast.meer {
			opacity: 0.5;
			&:hover {
				cursor: normal;
			}
		}
	}

	[data-dyslexie="1"] {
		.dyslexie.herstel {
			opacity: 1;
			&:hover {
				cursor: pointer;
			}
		}
		.dyslexie.meer {
			opacity: 0.5;
			&:hover {
				cursor: normal;
			}
		}
	}


