html {
	font-size: 16px;
}
	html[data-grootte='1'] {
		font-size: 20px;
	}
	html[data-grootte='2'] {
		font-size: 24px;
	}

body {
	color: $zwart;
	background-color: $heel_erg_palet;
	font-family: 'Merriweather', serif;
	margin-bottom: 40px;
}
	[data-contrast='1'] body{
		background-color: white;
		text-rendering: optimizeLegibility;
	}

[data-dyslexie='1'] *{
	font-family: Dyslexie, sans-serif !important;
	//font-size: 20px;
}

a:hover,
a:focus,
a:active {
	color: $rustig_rood;
}

a:hover {
	text-decoration: underline;
}

.klaar {
	&::after {
		content: '';
		display: block;
		clear: both;
	}
}

section, article {
	@extend .klaar;
}

.flex {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
}

.float {
	&::after {
		content: '';
		display: block;
		clear: both;
	}
	& > * {
		float: left;
	}
}
.rel {
	position: relative;
}

.verpakking{
	margin: 0 auto;
	padding: 0 20px;
	box-sizing: border-box;
	width: $desktop-vb;
	max-width: 100%;
}

.blok {
	background-color: white;
	box-shadow: 0 1px 2px $roodgrijze_tint;
	color: $donkergrijs;
}
	[data-contrast='1'] .blok {
		color: $zwart;
		box-shadow: none;
		//border: 1px solid grey;
	}

.knop-div ~ .knop-div{
	margin-top: 10px;
}

.knoppen-doos {
	margin-bottom: -10px;
	.knop {
		margin-bottom: 10px;
		margin-right: 10px;
	}
	hr {
		background-color: transparent;
		position: relative;
		&::after {
			position: absolute;
			left: 0;
			top: 4px;
			display: block;
			content: '';
			border-bottom: 1px solid $lichtgrijs;
			width: 290px;
			max-width: 80%;
		}
	}
}

.knop {
	display: inline-block;
	padding: 0.3em 1em;
	font-weight: bold;
	background-color: #fcfcfc;
	box-shadow: 2px 4px 0 $roodgrijze_tint;
	transition: all 0.2s ease-out 0.2s;
	position: relative;
	top: 0;
	color: $rustig_rood;
	&:hover, &:visited, &:active {
		color: $rustig_rood;
		text-decoration: none;
	}
	&:hover {
		transition: all 0.15s ease-out;
		box-shadow: 0.5px 1px 0 $roodgrijze_tint;
		top: 3px;
	}
	span {
		margin-right: 8px;
		color: $rustig_rood !important;
		line-height: inherit  !important;
	}
	i {
		color: $rustig_rood !important;
	}
	&.in-wit {
		background-color: $rustig_rood;
		box-shadow: 2px 4px 0 $roodgrijze_tint;
		color: white !important;
			&:hover {
			box-shadow: 0.5px 1px 0 $roodgrijze_tint;
			top: 3px;
		}
		span, i {
			color: white !important;
		}
	}
	&.ikoon-links {
		display: inline-flex;
		flex-direction: row;
		margin-right: 10px;
		span {
			order: 2;
			margin-right: 0;
			margin-left: 10px;
		}
		i {
			margin-left: -2px;
		}
	}
}

.buitengrens-rechts {
	position: relative;
	&::after {
		position: absolute;
		height: 100%;
		content: '';
		display: block;
		right: -10px;
		top: 0;
		border-right: 1px solid #ddd;
	}
}

a, a:visited {
	color: $rustig_rood;
	text-decoration: none;
}

h2, h3, h4, h5, h6 {
	&:first-child {
		margin-top: -0.3em;
	}
}

main > * {
	margin-bottom: 40px;
	&:last-child {
		margin-bottom: 0;
	}
}

section footer {
	text-align: right;
	margin-top: 10px;
}

.streep-eronder {
	display: inline-block;
	padding-right: 1.5em;
	border-style: solid;
	border-bottom-width: 2px;
	border-left: 0;
	border-right: 0;
	border-top: 0;
}

body [type='submit'] {
	@extend .knop;
	font-family: 'Merriweather', serif;
	font-size: 1em;
	border: 0;
	border-radius: 0;
	height: calc(2em + 2px);
}

[data-contrast='1'] {
	input[type="text"],
	input[type="email"],
	input[type="url"],
	input[type="password"],
	input[type="search"],
	input[type="number"],
	input[type="tel"],
	input[type="range"],
	input[type="date"],
	input[type="month"],
	input[type="week"],
	input[type="time"],
	input[type="datetime"],
	input[type="datetime-local"],
	input[type="color"],
	textarea {
		border: 1px solid $zwart;
		color: $zwart;
	}
}

.meta-niet-tonen {
	display: none;
}

::-moz-selection { /* Code for Firefox */
    color: white;
    background: lighten($rustig_rood, 15%);
}

::selection {
    color: white;
    background: lighten($rustig_rood, 15%);
}